* {
  box-sizing: border-box;
  color: #000;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-size: 12px;
}

body {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

:root {
  --link-color: #00acdc;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  text-align: center;
  text-transform: uppercase;
  padding: 23px 0;
  text-decoration: none;
  display: block;
}

.logo {
  width: 40px;
  height: 40px;
}

.is-active {
  color: var(--link-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover {
  color: var(--link-color);
}

